___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --backgroundColor: black;
}
html{
  margin: 0;
  padding: 0;
  color: white;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  height:100vh;
}
body{
  margin: 0;
  padding:0;
}
::selection {
  background-color: inherit;
}
dialog {
  width: 300px;
  height: 200px;
}
dialog::backdrop {
  background-color: rgba(0, 0, 0, 0.486);
}
.notification {
  padding-block: 20px;
}
.cancelButton {
  border-radius: 20px;
  background-color: rgba(0, 0, 255, 0.336);
}
main {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.mainImage {
  width: 700px;
  padding-top: 60px;
}
.absoluteDescription {
  position: absolute;
  font-size: 2rem;
  top: 0;
}
.middleside {
  position: relative;
}
#inputMessage {
  height: 3rem;
  font-size: 2.5rem;
}
.clickButton {
  height: 3rem;
  border-radius: 1rem;
  background-color: white;
}
.clickButton:hover {
  background-color: rgba(180, 180, 180, 0.322) !important;
  cursor: pointer;
}
.todayWeather {
  font-size: 30px;
  font-weight: bold;
}
.side {
  flex: 1;
}
form {
  display: flex;
  flex-direction: column;
}
header {
  padding-inline: 40px;
}
.descriptionSection {
}
.after {
  text-align: center;
}
.afterDescriptionContainer {
  display: flex;
  justify-content: space-around;
}
h3 {
  white-space: pre-line;
  padding-bottom: 100px;
}
footer {
  bottom: 0;
  color: white;
  background-color: black;
  display: flex;
  justify-content: center;
  position: fixed;
  bottom:0;
  left:0;
  right:0;
}
#loadingbackground {
  position: fixed;
  height: 100vh;
  width: 100vw;
  background-image: radial-gradient(
    rgb(0, 26, 255) 1%,
    #bdf5ff 30%,
    rgb(2, 40, 165) 70%
  );
  background-size: 300% 300%;
  background-position: center;
  animation: 2s linear 1 load;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}
@keyframes load {
  100% {
    background-size: 100% 100%;
    background-image: radial-gradient(
       rgb(0, 26, 255) 1%,
    #bdf5ff 70%,
    );
  }
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;EACE,wBAAwB;AAC1B;AACA;EACE,SAAS;EACT,UAAU;EACV,YAAY;EACZ,yDAAsD;EACtD,sBAAsB;EACtB,YAAY;AACd;AACA;EACE,SAAS;EACT,SAAS;AACX;AACA;EACE,yBAAyB;AAC3B;AACA;EACE,YAAY;EACZ,aAAa;AACf;AACA;EACE,sCAAsC;AACxC;AACA;EACE,mBAAmB;AACrB;AACA;EACE,mBAAmB;EACnB,wCAAwC;AAC1C;AACA;EACE,aAAa;EACb,sBAAsB;EACtB,mBAAmB;AACrB;AACA;EACE,YAAY;EACZ,iBAAiB;AACnB;AACA;EACE,kBAAkB;EAClB,eAAe;EACf,MAAM;AACR;AACA;EACE,kBAAkB;AACpB;AACA;EACE,YAAY;EACZ,iBAAiB;AACnB;AACA;EACE,YAAY;EACZ,mBAAmB;EACnB,uBAAuB;AACzB;AACA;EACE,uDAAuD;EACvD,eAAe;AACjB;AACA;EACE,eAAe;EACf,iBAAiB;AACnB;AACA;EACE,OAAO;AACT;AACA;EACE,aAAa;EACb,sBAAsB;AACxB;AACA;EACE,oBAAoB;AACtB;AACA;AACA;AACA;EACE,kBAAkB;AACpB;AACA;EACE,aAAa;EACb,6BAA6B;AAC/B;AACA;EACE,qBAAqB;EACrB,qBAAqB;AACvB;AACA;EACE,SAAS;EACT,YAAY;EACZ,uBAAuB;EACvB,aAAa;EACb,uBAAuB;EACvB,eAAe;EACf,QAAQ;EACR,MAAM;EACN,OAAO;AACT;AACA;EACE,eAAe;EACf,aAAa;EACb,YAAY;EACZ;;;;GAIC;EACD,0BAA0B;EAC1B,2BAA2B;EAC3B,2BAA2B;EAC3B,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,UAAU;AACZ;AACA;EACE;IACE,0BAA0B;IAC1B;;;KAGC;EACH;AACF","sourcesContent":[":root {\r\n  --backgroundColor: black;\r\n}\r\nhtml{\r\n  margin: 0;\r\n  padding: 0;\r\n  color: white;\r\n  background-image: url('./asset/background/night.avif');\r\n  background-size: cover;\r\n  height:100vh;\r\n}\r\nbody{\r\n  margin: 0;\r\n  padding:0;\r\n}\r\n::selection {\r\n  background-color: inherit;\r\n}\r\ndialog {\r\n  width: 300px;\r\n  height: 200px;\r\n}\r\ndialog::backdrop {\r\n  background-color: rgba(0, 0, 0, 0.486);\r\n}\r\n.notification {\r\n  padding-block: 20px;\r\n}\r\n.cancelButton {\r\n  border-radius: 20px;\r\n  background-color: rgba(0, 0, 255, 0.336);\r\n}\r\nmain {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n}\r\n.mainImage {\r\n  width: 700px;\r\n  padding-top: 60px;\r\n}\r\n.absoluteDescription {\r\n  position: absolute;\r\n  font-size: 2rem;\r\n  top: 0;\r\n}\r\n.middleside {\r\n  position: rela